import DirectoryTreePicker from '@/components/DirectoryTreePicker.vue';
import fetchWithAuth from '@/utils/fetchWithAuth';
export default {
  name: 'MoveFileDialog',
  components: {
    DirectoryTreePicker
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    currentDirectory: {
      type: String,
      default: ''
    },
    isBatchMove: {
      type: Boolean,
      default: false
    },
    initialPath: {
      type: String,
      default: '/'
    }
  },
  emits: ['update:modelValue', 'confirm', 'close'],
  data() {
    return {
      targetPath: '/',
      loading: false,
      windowWidth: window.innerWidth,
      // 
      showTreePicker: false,
      treeData: [],
      treeLoading: false,
      treeError: '',
      treeProps: {
        children: 'children',
        label: 'name'
      }
    };
  },
  computed: {
    dialogVisible: {
      get() {
        return this.modelValue;
      },
      set(val) {
        this.$emit('update:modelValue', val);
      }
    },
    isDesktop() {
      return this.windowWidth > 768;
    }
  },
  watch: {
    modelValue(newVal) {
      if (newVal) {
        this.targetPath = this.initialPath || '/';
        this.showTreePicker = false;
      }
    },
    showTreePicker(newVal) {
      if (newVal && !this.isDesktop) {
        this.fetchDirectoryTree();
      }
    }
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize() {
      this.windowWidth = window.innerWidth;
    },
    handleDirectorySelect(path) {
      this.targetPath = path;
    },
    handleConfirm() {
      this.$emit('confirm', this.targetPath);
    },
    handleClose() {
      this.$emit('update:modelValue', false);
      this.$emit('close');
    },
    // 
    async fetchDirectoryTree() {
      this.treeLoading = true;
      this.treeError = '';
      try {
        const response = await fetchWithAuth('/api/directoryTree', {
          method: 'GET'
        });
        if (!response.ok) {
          if (response.status === 401) {
            this.treeError = '';
          } else {
            this.treeError = '';
          }
          return;
        }
        const data = await response.json();
        if (data.tree) {
          this.treeData = [data.tree];
        } else {
          this.treeData = [];
        }
      } catch (err) {
        console.error('Failed to fetch directory tree:', err);
        this.treeError = '';
      } finally {
        this.treeLoading = false;
      }
    },
    handleTreeNodeClick(data) {
      let path = data.path;
      if (path && !path.startsWith('/')) {
        path = '/' + path;
      } else if (!path) {
        path = '/';
      }
      this.targetPath = path;
      this.showTreePicker = false;
    }
  }
};